      });
    setTimeout(() => {
      that.secretDialog = !localStorage.getItem('ifused')
    }, 1000);
  },
  mounted() {
  },
  methods: {
    onScroll(mescroll, y, isUp) {
      this.$store.change_scrollY(y);
      this.$store.change_isUp(isUp);
    },
    mescrollInit(mescroll) {
      this.mescroll = mescroll; // 如果this.mescroll对象没有使用到,则mescrollInit可以不用配置
    },
    showBottomDrawer(i) {
      this.bottomDrawer.showBottomDrawer = true;
      this.bottomDrawer.chooseIndex = i;
      this.bottomDrawer.dislike_num = this.items[i].dislike_num;
      console.log(this.bottomDrawer);
    },
    //计算时间差
    timeFormat(latest_comment_time) {
      //一共这么多秒前
      let t = Math.floor(
        (new Date().getTime() - new Date(latest_comment_time).getTime()) / 1000
      );
      const d = Math.floor(t / 3600 / 24);
      const h = Math.floor(t / 3600) % 24;
      const m = Math.floor(t / 60) % 60;
      const s = Math.floor(t % 60);
      let res = "";
      if (s > 0 && m == 0 && h == 0 && d == 0) {
        res = s + "秒前";
      } else if (m > 0 && h == 0 && d == 0) {
        res = m + "分钟前";
      } else if (h > 0 && d == 0) {
        res = h + "小时前";
      } else {
        let date = new Date(
          new Date(latest_comment_time).getTime() + 8 * 3600 * 1000
        );
        return date.toJSON().substr(0, 16).replace("T", " ");
      }
      return res;
    },
    //获取屏蔽配置
    getShield() {
      try {
        var shieldTag = localStorage.getItem('shieldTag')
        var shieldTagMethod = Number(localStorage.getItem('shieldTagMethod'))
        var shieldPostWordMethod = Number(localStorage.getItem('shieldPostWordMethod'))
        var shieldPostWord = localStorage.getItem('shieldPostWord')
        return { shieldTag, shieldTagMethod, shieldPostWord, shieldPostWordMethod }
      } catch (e) {
        return { shieldTag, shieldTagMethod, shieldPostWord, shieldPostWordMethod }
      }
    },
    getMorePosts(page, mescroll) {
      console.log(page);
      console.log("test");
      let that = this;
      that.$request('get', '/forum/getPosts', {
        timeToken: new Date(that.items[that.items.length - 1].latest_comment_time).getTime(),
        category: that.category.selected,
      })
        .then((res) => {
          if (page.num === 1) that.items = []
          that.$nextTick(() => {
            mescroll.endSuccess(res.data.length);
          });
          that.items.push(...res.data);
        })
        .catch(e => {
          console.log(e)
          that.showSnackbar('获取失败')
          mescroll.endErr();
        })
    },
    refreshData(mescroll) {
      let that = this;
      // let { shieldTag, shieldTagMethod, shieldPostWord, shieldPostWordMethod } = that.getShield()
      // console.log(shieldTag, shieldPostWord)
      this.$request("get", "/forum/getTopPosts", {}, {})
      .then((res) => {
        that.topPosts = res.data;
        that.topPosts.map((item) => (item.top = true));
      })
      .catch((e) => {
        that.showSnackbar(e);
      });
      that.$request('get', '/forum/getPosts', {
        timeToken: new Date().valueOf(),
        category: that.category.selected,
      })
        .then((res) => {
          that.items = res.data;
          console.log(that.items)
          that.$nextTick(() => {
            mescroll.endSuccess(res.data.length);
          });
        })
        .catch(e => {
          console.log(e)
          that.showSnackbar(e)
          mescroll.endErr()
        })
    },
    likeOrDislike(i, value) {
      let that = this;
      that.$request('post', '/forum/forumLikeOrDislike', {}, {
        postId: Number(that.items[i].id),
        value: value,
      })
        .then((res) => {
          that.showSnackbar(res, "ok");
          if (value == 1) that.items[i].like_num++;
          else if (value == -1) {
            that.bottomDrawer.dislike_num++;
            that.items[i].dislike_num++;
          }
        })
        .catch(e => {
          console.log(e)
          that.showSnackbar(e)
        })
    },
    addCollect(i) {
      let that = this;
      that.$request('post', '/forum/addCollect', {}, {
        postId: Number(that.items[i].id),
      })
        .then((res) => {
          that.showSnackbar(res, "ok");
        })
        .catch(e => {
          that.showSnackbar(e);
        })
    },
    secretDialogConfirm(v) {
      if (v) {
        localStorage.setItem('ifused', true)
        this.secretDialog = false
      }
      else {
        this.showSnackbar('感谢使用,即将退出程序')
        setTimeout(() => {
          navigator.app.exitApp()
        }, 1800);
      }
    }
  },
  beforeDestroy() {
    clearInterval(this.timer);
  },
  // beforeRouteLeave(to, from, next) {
  //   // if (to.meta.index != 0) {
  //   //   from.meta.keepAlive = true;
  //   // } else {
  //   //   from.meta.keepAlive = false;
  //   //   // this.$destroy()
  //   // }
  //   // next();
  // },
  activated() {
    //进入时读取位置
    // document.body.scrollTop = this.scrollY;
    // document.documentElement.scrollTop = this.scrollY;
    console.log(this.$store.state.scrollYLeave);
    this.mescroll.scrollTo(this.$store.state.scrollYLeave, 0);
  },
  // deactivated() {
  //   //离开时记录位置
  //   this.scrollY = this.scrollY;
  // },
  watch: {
    // "bottomDrawer.showBottomDrawer"(v) {
    //   this.$store.change_drawer(v, "postsList");
    // },
    // "$store.state.drawer"(v) {
    //   if (this.$store.state.drawerName == "postsList")
    //     this.bottomDrawer.showBottomDrawer = v;
    // },
    'openWhyFoldedDialog'(v) {
      this.$store.change_dialog(v, 'openWhyFoldedDialog')
    },
    'secretDialog'(v) {
      this.$store.change_dialog(v, 'secretDialog')
    },
    '$store.state.dialog'(v) {
      if (this.$store.state.dialogName == 'openWhyFoldedDialog')
        this.openWhyFoldedDialog = v
      else if (this.$store.state.dialogName == 'secretDialog')
        this.secretDialog = v
    }
  },
};</script>

<style>
.mescroll_container {
  position: fixed;
  top: calc(60px+env(safe-area-inset-top));
  bottom: 0;
  height: auto; /*如设置bottom:50px,则需height:auto才能生效*/
  width: 100%;
}
.edit {
  z-index: 99;
  bottom: 10px;
  right: 20px;
}
.postList_container {
  background-color: #f5f5f5;
}
.tag_on_card {
  position: absolute;
  top: -10px;
  left: 10px;
  z-index: 2;
}
.top_card_arrow_right {
  position: absolute;
  right: 10px;
  top: 70px;
  color: rgba(61, 60, 60, 0.493);
}
.top_card_arrow_left {
  position: absolute;
  left: 10px;
  top: 64px;
  color: rgba(61, 60, 60, 0.493);
}
.top_card_carousel {
  position: absolute;
  width: 100%;
  height: 100%;
}
.top_card_title_1 {
  position: absolute;
  bottom: 0;
}
.top_card_title_2 {
  position: absolute;
  display: flex;
  flex-direction: column;
  left: 30px;
  top: 30px;
}
.top_card_background {
  right: 0;
  top: 0;
  /* -webkit-filter: blur(1.5px);
  filter: blur(1.5px); */
}
.card_background_1 {
  left: calc(10% - 20px);
  margin-top: -6px;
}
.card_background_2 {
  left: calc(11% - 2px);
  margin-top: -6px;
}
.fold_text {
  position: absolute;
  left: 57vw;
  top: 54px;
}
.refresh_message {
  position: absolute;
  width: 100%;
}
</style>


